import Head from "next/head";
import global from "../data/global.json";

const NavButton = props => (
    <a href={props.link} className={"navButton" + (props.page==props.title ? " active" : "")}>
        {props.title}
    </a>
);

const Layout = props => (
    <div className="main">
        <Head>
            <title>{props.page}</title>
            <link href="https://fonts.googleapis.com/css?family=Source+Sans+Pro" rel="stylesheet" />
        </Head>
        <div className="header">
            <NavButton page={props.page} title="Home" link="/" />
            <NavButton page={props.page} title="Projects" link="/projects" />
            <NavButton page={props.page} title="Contact" link="/contact" />
        </div>
        <div className="content">
            {props.children}
        </div>
        <div className="footer">
            This website is under construction. Please pardon the dust.
        </div>
        <style jsx global>{`
            * {
                font-family: 'Source Sans Pro', sans-serif;
            }
            body {
                margin: 0px;
                background-color: ${global.colors.backgroundDark};
            }
            .main {
                width: 100%;
                min-height: 100vh;
            }
            .header {
                height: ${global.constants.headerHeight}px;
                position: fixed;
                width: 100%;
                z-index: 100;
                display: flex;
            }
            .navButton {
                flex-grow: 1;
                text-align: center;
                font-size: 24px;
                background-color: ${global.colors.backgroundDark};
                will-change: background-color;
                transition: 0.2s;
                line-height: ${global.constants.headerHeight}px;
                color: ${global.colors.textLight};
                text-decoration: none;
            }
            .navButton.active {
                background-color: ${global.colors.accentDark};
            }
            .navButton:hover {
                background-color: ${global.colors.accentLight};
            }
            .content {
                background-color: ${global.colors.backgroundLight};
                color: ${global.colors.textDark};
                height: fit-content;
                min-height: calc(100vh - ${global.constants.headerHeight + global.constants.footerHeight}px);
                position:relative;
            }
            .footer {
                background: ${global.colors.backgroundDark};
                color: ${global.colors.textLight};
                height: ${global.constants.footerHeight}px;
                padding: 20px;
                box-sizing: border-box;
            }
            .footer, .content {
                position: relative;
                top: ${global.constants.headerHeight}px;
            }


            @media only screen and (max-width: ${global.constants.mobileWidth}px) {
                .header {
                    display: block;
                    position: static;
                    height: ${global.constants.headerHeight * global.constants.numNavButtons}px;
                }
                .navButton {
                    width: 100%;
                    display: block;
                }
                .footer, .content {
                    position: static;
                }
            }
        `}</style>
    </div>
)

export default Layout;